<template>
  <div style="padding-bottom: 100px;">
    <div class="function-con-new">
      <p class="function-h-new gradient2 pdl-20">Search Result</p>
      <section class="comment-block mgt-20">
        <div class="comment-con">
          <p class="comment-h">{{ textareText }}</p>
          <img class="resultImg" v-bind:src="this.imgSrc" alt="">
          <p class="comment-m mgt-20">{{ des }}</p>
        </div>
        <div class="comment-b">
          <div class="comment-data mgt-20 where-col" @click="goMap()">
            <img src="../assets/images/map-new.png" alt="">
            Go {{ textareText }}
          </div>
        </div>
      </section>
      <div class="function-con mgt-20">
        <p class="function-h-2">Nearby Attraction</p>
        <div class="all-con">
          <div v-for="(item) in nearBy" :key="item.id" class="fun-tag">
            <p class="tag-h1">{{ item }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'searchResult',
  components: {},
  data () {
    return {
      msg: '',
      textareText: String,
      nearBy: [],
      imgSrc: String,
      des: String
    }
  },
  created: function () {
    this.getParams()
    this.searchFor()
  },
  watch: {
    '$route': 'getParams'
  },
  methods: {
    getParams: function () {
      var routerParams = this.$route.query.searchInf
      this.textareText = routerParams
    },
    searchFor: function () {
      var that = this
      that.$http.post('http://localhost:8088/api/searchFor', {
        locationName: this.textareText
      }).then(function (response) {
        console.log(response)
        this.des = response.data.des
        this.imgSrc = response.data.src
        for (let i in response.data.nearby) {
          this.nearBy.push(response.data.nearby[i])
        }
      }, function (err) {
        console.log(err)
      })
    },
    goMap: function () {
      this.$router.push({path: 'goWhere', query: {targetLoc: this.textareText}})
    }
  }
}

</script>



