<template>
  <div style="padding-bottom: 100px;">
    <div class="function-con-new">
      <p class="function-h-new gradient2 pdl-20">Translation</p>
    </div>
    <div class="translateIn">
      <textarea type="text" ref="inputBlock" value="test"></textarea>
    </div>
    <div class="translateOut">
      <p>Result:{{ transResult }}</p>
    </div>
    <div class="translateBtn login-btn" @click="translateBtn()">translate</div>
    <footerNav :tagFooter="footerVal"></footerNav>
  </div>
</template>

<script>
// import art from '../components/art'
import footerNav from '../components/footer-nav'

export default {
  name: 'translate',
  components: {
    footerNav
  },
  data () {
    return {
      transResult: 'test test',
      transCon: null,
      footerVal: 3
    }
  },
  methods: {
    translateBtn: function () {
      var that = this
      this.transCon = this.$refs.inputBlock.value
      console.log(this.transCon)
      that.$http.post('http://localhost:8088/api/translate', {
        textcon: this.transCon
      }).then(function (response) {
        console.log(response)
        this.transResult = response.data.translation
      }, function (err) {
        console.log(err)
      })
    }
  }
}

</script>



